.hamburger {
  padding: 16px 15px 11px 15px;
  display: inline-block;
  cursor: pointer;
  transition-property: opacity, filter;
  transition-duration: 0.15s;
  transition-timing-function: linear;
  font: inherit;
  color: inherit;
  text-transform: none;
  background-color: transparent;
  border: 0;
  margin: 0;
  overflow: visible;
  &:hover {
    opacity: 0.7; } }

.hamburger-box {
  width: 14px;
  height: 12px;
  display: inline-block;
  position: relative; }

.hamburger-inner {
  display: block;
  top: 50%;
  margin-top: -2px;
  width: 14px;
  height: 2px;
  background-color: #c7c7c7;
  border-radius: 2px;
  position: absolute;
  transition-property: transform;
  transition-duration: 0.15s;
  transition-timing-function: ease;
  &::before, &::after {
    width: 14px;
    height: 2px;
    background-color: #c7c7c7;
    border-radius: 2px;
    position: absolute;
    transition-property: transform;
    transition-duration: 0.15s;
    transition-timing-function: ease; }
  &::before, &::after {
    content: "";
    display: block; }
  &::before {
    top: -5px; }
  &::after {
    bottom: -5px; } }

/* * * Vortex */

.hamburger--vortex {
  .hamburger-inner {
    transition-duration: 0.2s;
    transition-timing-function: cubic-bezier(0.19, 1, 0.22, 1);
    &::before, &::after {
      transition-duration: 0s;
      transition-delay: 0.1s;
      transition-timing-function: linear; }
    &::before {
      transition-property: top, opacity; }
    &::after {
      transition-property: bottom, transform; } }
  &.is-active .hamburger-inner {
    transform: rotate(765deg);
    transition-timing-function: cubic-bezier(0.19, 1, 0.22, 1);
    &::before, &::after {
      transition-delay: 0s; }
    &::before {
      top: 0;
      opacity: 0; }
    &::after {
      bottom: 0;
      transform: rotate(90deg); } } }
